<template>
    <div class="tabs-wrap">
        <div class="tabs-title"><i class="el-icon-arrow-right"></i>收款情况</div>
        <div class="tabs-charts">
            <div class="min-max">
                <span class="min">0</span>
                <span class="max">500</span>
            </div>
            <v-chart autoresize :option="option" />
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            option: {
                series: [
                    {
                        type: "gauge",
                        startAngle: 200,
                        endAngle: -20,
                        min: 0,
                        max: 500,
                        splitNumber: 12,
                        itemStyle: {
                            color: "#58D9F9",
                        },
                        progress: {
                            show: true,
                            width: 35,
                        },
                        pointer: {
                            show: false,
                        },
                        axisLine: {
                            show: true,
                            lineStyle: {
                                width: 35,
                                color: [[1, "#051c48"]],
                            },
                        },
                        axisTick: {
                            show: false,
                        },
                        splitLine: {
                            show: false,
                        },
                        axisLabel: {
                            show: false,
                        },
                        data: [
                            {
                                value: 250,
                            },
                        ],
                        detail: {
                            color: '#00edff',
                            fontSize: 14,
                            fontWeight: 400,
                            lineHeight: 20,
                            formatter: function (value) {
                                return `\n${value / 500 * 100}%\n收款情况`
                            }
                        },
                    },
                ],
            },
        };
    },
};
</script>
<style lang="scss">
.min-max {
    position: relative;
    color: #fff;
    z-index: 1;
    .min {
        position: absolute;
        left: 50px;
        top: 70px;
    }
    .max {
        position: absolute;
        right: 50px;
        top: 70px;
    }
}
</style>
