<template>
    <el-table 
        :key="tableKey" 
        :data="dataSource.filter((o,i) => option.count ? i < option.count : true)"
        :row-style="setRowStyle"
        :show-header="!option.hideHeader"
        :header-cell-style="{
            padding: '12px 0', 
            backgroundColor: option.headerBackground + ' !important',
            fontSize: option.fontSize + 'px !important'
        }"
    >
        <el-table-column 
            v-for="item in option.column" 
            :key="item.prop" 
            :label="item.label" 
            :prop="item.prop" 
            :width="item.width"
            :align="option.headerTextAlign"
        >
            <template slot="header" slot-scope="{ column }">
                <div
                    :style="{
                        color: option.headerColor,
                    }"
                >{{ column.label }}</div>
            </template>
        </el-table-column>
    </el-table>
</template>
<script>
export default {
    name: "custom-table",
    inject: ["main"],
    props: {
        url: String,
        dataType: Number,
        dataMethod: String,
        dataQuery: Object,
        data: Array,
        option: Object,
        component: Object,
    },
    mounted(){
        this.updateData()
    },
    data(){
        return {
            dataSource: this.data,
            tableKey: new Date().getTime()
        }
    },
    methods: {
        setRowStyle({rowIndex}){
            return {
                color: this.option.bodyColor + ' !important',
                backgroundColor: (rowIndex % 2 === 0 ? this.option.nthColor : this.option.othColor) + ' !important',
                fontSize: this.option.fontSize + 'px !important'
            }
        },
        updateData(){
            let that = this
            const { dataType, dataMethod, url, dataQuery} = that;
            if(dataType){
                this.$axios[dataMethod](url, dataMethod === 'get' ? {params: dataQuery} : {...dataQuery}).then(res => {
                    that.dataSource = res.data.data
                })
            }else{
                this.dataSource = this.data;
            }
        }
    }
}
</script>
<style lang="scss">
.el-table__header-wrapper{
    th{
        padding: 0;
        // height: 30px;
        .cell{
            padding-left: 0;
            padding-right: 0;
        }
    }
}
</style>