// import { Loading } from 'element-ui';
import axios from 'axios';
import { getCookieValue } from './tool'

const currentServerHost = localStorage.getItem("currentServerHost");
axios.defaults.baseURL = currentServerHost || `${window.location.origin}/restcloud`;
axios.defaults.timeout = 20000;
//返回其他状态吗
axios.defaults.validateStatus = function (status) {
  return status >= 200 && status <= 500; // 默认的
};
//跨域请求，允许保存cookie
// let loadingInstance = '';
axios.defaults.withCredentials = false;
axios.interceptors.request.use(config => {
  config.headers.identitytoken = getCookieValue('identitytoken')
  // loadingInstance = Loading.service({
  //   text: '拼命加载中',
  //   background: 'rgba(0,0,0,0.5)',
  //   spinner: 'el-icon-loading'
  // });
  return config
}, error => {
  return Promise.reject(error)
});
//HTTPrequest拦截
axios.interceptors.response.use(res => {
  console.log('res', res);
  // loadingInstance.close();
  return Promise.resolve(res);
}, error => {
  return Promise.reject(new Error(error));
})

export default axios;