<template>
    <div class="title-bar" :style="{
        width: component.width + 'px',
        height: component.height + 'px',
        
        backgroundImage: backgroundImg
    }">
        <div class="title-bg" :style="{
            width: '100%',
            height: '100%',
            backgroundImage: background,
        }">
            <div class="title-bg-before" v-if="option.titleLeftBg" :style="{
                background: `url(${option.titleLeftBg}) no-repeat`,
                transform: `rotate(${option.leftRotate || 0}deg) scale(${option.scale})`
            }"></div>
            <div class="title-content" :style="{
                color: option.color,
                fontSize: option.fontSize + 'px',
                fontWeight: option.fontWeight,
                textAlign: option.textAlign
            }">{{option.text}}</div>
            <div class="title-bg-after" v-if="option.titleRightBg" :style="{
                background: `url(${option.titleRightBg}) no-repeat`,
                transform: `rotate(${option.rightRotate || 0}deg) scale(${option.scale})`
            }"></div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'title-bar',
    props: {
        option: Object,
        component: Object,
    },
    computed: {
        background(){
            return `url(${this.option.titleBg})`
        },
        backgroundImg(){
            const { color1, color2, position } = this.option.gradientList
            return color1 && color2 ? `linear-gradient(to right, ${color1} ${position}%, ${color2})` : ''
        }
    }
}
</script>
<style lang="scss">
.title-bar{
    height: 62px;
    box-sizing: border-box;
    .title-bg{
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center center;
        .title-bg-before{
            width: 112px;
            height: 20px;
        }
        .title-content{
            flex: 1 1;
        }
        .title-bg-after{
            width: 112px;
            height: 20px;
        }
    }
}
</style>