<template>
    <el-select 
        v-model="value"
        :style="{
            width: component.width + 'px',
            height: component.height + 'px',
        }"
        :multiple="option.multiple"
    >
        <el-option
            v-for="item in option.options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
        >
        </el-option>
    </el-select>
</template>
<script>
export default {
    name: 'select-picker',
    props: {
        option: Object,
        component: Object,
    },
    inject: ["main"],
    data(){
        return {
            value: ''
        }
    }
}
</script>
