<template>
    <div class="tabs-bar-wrapper" :class="`tabs-bar-${option.type}`" :ref="common.NAME+objIndex"> 
        <template v-if="option.type === 'dot'" >
            <el-button 
                class="tabs-item" 
                v-for="(item, index) in option.list" 
                :key="`tab-${index}`" 
                :class="active === item.value ? 'tabs-item-active' : ''"
                @click="clickItem(item)"
                
                :style="{
                    width: index !== option.list.length - 1 && `calc(100% / ${option.list.length - 1} - 42px)`
                }"
            >
                <span class="tabs-item-icon">
                    <span :style="{
                        backgroundImage: background
                    }"></span>
                </span>
                <span class="tabs-item-text" :style="{
                    color: option.textColor,
                    fontSize: option.fontSize + 'px',
                }">{{item.label}}</span>
            </el-button>
        </template>
        <template v-if="option.type === 'circle'">
            <div class="tabs-bar-position">
                <div class="tabs-item" 
                    v-for="(item, index) in option.list" 
                    :key="item.value" 
                    :class="active === item.value ? 'tabsActive' : ''"
                    @click="clickItem(item)"
                    :style="{width: `calc(100% / ${option.list.length})`}"
                >
                    <el-button 
                        class="tabs-item-btn" 
                        :style="{
                            fontSize: option.fontSize + 'px',
                            transform: `scale(${option.size})`
                        }"
                    >
                        {{item.label}}
                    </el-button>
                </div>
                <span 
                    class="tabs-bar-tag"
                    :style="{
                        width: `calc(100% / ${option.list.length})`,
                        left: `calc(100% / ${option.list.length} * ${option.list.findIndex(o => o.value === active)})`
                    }"
                ></span>
            </div>
        </template>
    </div>
</template>
<script>
import common from '@/config'
export default {
    name: 'tabs-bar',
    props: {
        child: Object,
        option: Object,
        component: Object,
    },
    inject: ["contain"],
    data(){
        return {
            common,
            objIndex: this.child.index ? this.child.index[0] : undefined,
        }
    },
    computed: {
        active(){
            const index = this.option.list.findIndex(o => o.value == this.option.active)
            return this.option.list[index >= 0 ? index : 0].value
        },
        background(){
            const { color1, color2, position } = this.option.gradientList
            return color1 && color2 ? `linear-gradient(to top, ${color1} ${position}%, ${color2})` : ''
        }
    },
    methods: {
        clickItem(item){
            const { value, linkHref, bigScreenId } = item
            this.active = value;
            this.goLink(linkHref, bigScreenId)
        },
        goLink(linkHref, bigScreenId){
            const { name } = this.$route
            if(bigScreenId){
                window.location.href = `${window.location.origin + window.location.pathname}#/${name !== "view" ? 'view' :'viewCopy'}/${bigScreenId}`
            }else if(linkHref){
                window.open(linkHref)
            }
        }
    }
}
</script>
<style lang="scss">
.tabs-bar-dot{
    display: flex;
    justify-content: space-between;
    .tabs-item{
        display: flex;
        flex-flow: column;
        border: none;
        &.tabs-item-active{
            background: none;
            .tabs-item-icon{
                border: 1px solid #0099ff;
                box-shadow: 0 0 16px rgba(52, 192, 217, .78) inset, 0 0 20px rgba(123, 217, 237, .7);
            }
        }
        .tabs-item-icon{
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 12px;
            width: 26px;
            height: 26px;
            margin: 0 auto 20px;
            border-radius: 30px;
            border: 1px solid #0099ff;
            span{
                display: block;
                width: 22px;
                height: 22px;
                border-radius: 11px;
            }
        }
        &:not(:last-child){
            position: relative;
            &::after{
                content: '';
                position: absolute;
                width: calc(100% - 105px);
                height: 4px;
                right: 0;
                top: 39px;
                background-image: linear-gradient(to bottom, #33f0f2, #0099ff)
            }
        }
    }
    .el-button:focus, .el-button:hover{
        background: none;
    }
}
.tabs-bar-none{
    .tabs-bar-radio{
        display: flex;
        .el-radio-button{
            flex: 1 0;
            &.is-active{
                .el-radio-button__inner{
                    background: #409EFF;
                }
            }
            .el-radio-button__inner{
                width: 100%;
                height: 100%;
                color: #fff;
                font-size: inherit;
                background: transparent;
                border-color: #409EFF;
            }
        }
    }
}
.tabs-bar-circle{
    .tabs-bar-position{
        position: relative;
        display: flex;
        justify-content: space-between;
        .tabs-item{
            display: flex;
            justify-content: center;
            align-items: center;
            &:not(.tabsActive){
                opacity: .5;
            }
            .tabs-item-btn{
                width: 88px;
                height: 88px;
                background: url('../../assets/tabs-bg.png') no-repeat;
                border: none;
                color: #00aeef;
            }
        }
        .tabs-bar-tag{
            position: absolute;
            height: 6px;
            background: #00aeef;
            top: 110px;
            transition: all .3s linear;
            &::before{
                content: '';
                position: absolute;
                display: block;
                border: 8px solid;
                width: 0;
                height: 0;
                left: 0;
                right: 0;
                bottom: 6px;
                margin: 0 auto;
                border-color: transparent transparent #00aeef transparent;
            }
        }
    }
}
</style>