export const dataToChart = (type, dataSource, fd, opts) => {
    const { data } = dataSource
    let newData = data
    console.log(newData);
    if(!(data instanceof Array)){
        newData = data.data || data || []
    }
    if(typeof newData === 'string'){
        return false
    }
    if(type === 'bar' || type === 'line'){
        const categories = [...new Set(newData.map(item => item[fd.categories]))]
        const name = newData.map(item => item[fd.name])
        const obj = {
            categories: categories,
            series: [...new Set(name)].map((item, ind) => {
                return {
                    name: item,
                    yAxisIndex: (()=>{
                        const obj = newData.filter(o => o[fd.name] === item)
                        return fd.yAxisIndex ? obj[0][fd.yAxisIndex] : undefined
                    })(),
                    data: categories.map(cItem => {
                        const index = newData.findIndex(o => o[fd.name] === item && o[fd.categories] === cItem)
                        return index >= 0 ? newData[index][fd.value] : 0
                    })
                }
            })
        }
        return obj
    }
    if(type === 'pie'){
        return newData.map(item => ({
            name: item[fd.name],
            value: item[fd.value]
        }))
    }
    if(type === 'radar'){
        const name = [...new Set(newData.map(item => item[fd.categories]))]
        const indicator = [];
        newData.forEach(item => {
            const index = indicator.findIndex(o => o.name === item[fd.name])
            if(index < 0){
                indicator.push({
                    name: item[fd.name],
                    max: item[fd.value] <= 1 ? 100 : undefined
                })
            }
        });
        return {
            indicator,
            series: [
                {
                    data: name.map(n => ({
                        name: n,
                        value: [...newData.filter(o => o[fd.categories] === n)].map(item => item[fd.value])
                    }))
                }
            ]
        }
    }
    if(type === "scatter"){
        if(opts.highlight){
            const data1 = newData.filter(o => o.HIGHLIGHT);
            const data2 = newData.filter(o => !o.HIGHLIGHT);
            return [
                {
                    data: data2.map((item, index) => ({
                        name: fd.name ? item[fd.name] : `数据${index + 1}`,
                        value: [item[fd.x], item[fd.y]]
                    }))
                },
                {
                    data: data1.map((item, index) => (
                        {
                            name: fd.name ? item[fd.name] : `数据${data2.length + index}`,
                            value: [item[fd.x], item[fd.y]]
                        }
                    ))
                    
                },
            ]
        }else{
            return [
                {
                    data: newData.map((item, index) => {
                        return {
                            name: fd.name ? item[fd.name] : `数据${index + 1}`,
                            value: [item[fd.x], item[fd.y]]
                        }
                    })
                }
            ]
        }
    }
    if(type === "text"){
        return {
            value: newData[fd.value]
        }
    }
    if(type === 'color'){
        if(!(data instanceof Array)){
            return [newData].map(item => ({
                backgroundColor: item[fd.backgroundColor],
                prefixText: item[fd.prefixText],
                value: item[fd.value],
                suffixText: item[fd.suffixText]
            }))
        }else{
            return newData.map(item => ({
                backgroundColor: item[fd.backgroundColor],
                prefixText: item[fd.prefixText],
                value: item[fd.value],
                suffixText: item[fd.suffixText]
            }))
        }
    }
    if(type === "flop"){
        return {
            value: newData[fd.value]
        }
    }
    if(type === 'progress'){
        return {
            label: newData[fd.name] ? newData[fd.name] : fd.name,
            data: newData[fd.value]
        }
    }
    if(type === "select"){
        if(!(data instanceof Array)){
            return [newData].map(item => ({
                label: item[fd.name],
                value: item[fd.value]
            }))
        }else{
            return newData.map(item => ({
                label: item[fd.name],
                value: item[fd.value]
            }))
        }
    }
    if(type === "img"){
        return {
            "value": newData[fd.value]
        }
    }
    if(type === 'tabs'){
        return newData.map(item => ({
            value: item[fd.value],
            label: item[fd.label],
        }))
    }
    if(type === "wordcloud"){
        return newData.map(item => ({
            name: item[fd.name],
            value: item[fd.value]
        }))
    }
}

export const getCookieValue = (field) => {
	const cook = document.cookie.split('; ');
	const cookieIndex = cook.findIndex(o => o.match(field) !== null);
	const fieldValue = cookieIndex >= 0 ? cook[cookieIndex].split('=')[1] : undefined;
	return fieldValue;
};