                    <property-info />
                </el-tab-pane>
                <el-tab-pane  name="lease-condition">
                    <div class="custom-tabs-label" slot="label">
                        <lease-condition-tabs />
                    </div>
                    <lease-condition />
                </el-tab-pane>
                <el-tab-pane  name="proceeds-condition">
                    <div class="custom-tabs-label" slot="label">
