<script>
import landInfo from "./landInfo.vue"
import propertyInfo from "./propertyInfo.vue"
import leaseCondition from "./leaseCondition.vue"
import proceedsCondition from "./proceedsCondition.vue"
import landInfoTabs from './tabs/landInfoTabs.vue'
import propertyInfoTabs from './tabs/propertyInfoTabs.vue'
import leaseConditionTabs from './tabs/leaseConditionTabs.vue'
import proceedsConditionTabs from './tabs/proceedsConditionTabs.vue'
export default {
    name: "custom-tabs",
    props: {
        component: Object,
    },
    components: {
