<template>
    <div class="graph-wrapper">
        <div class="graph" :style="{
            width,
            height,
            borderWidth,
            borderStyle,
            borderColor,
            backgroundColor,
            borderRadius
        }"></div>
    </div>
</template>
<script>
export default {
    name: "graph",
    props: {
        option: Object,
        component: Object,
    },
    computed: {
        width(){
            return this.component.width + 'px'
        },
        height(){
            return this.component.height + 'px'
        },
        borderWidth(){
            return (this.option.borderWidth || 0) + 'px'
        },
        borderStyle(){
            return this.option.borderStyle || 'solid'
        },
        borderColor(){
            return this.option.borderColor || '#000'
        },
        backgroundColor(){
            return this.option.backgroundColor || 'transparent'
        },
        borderRadius(){
            return !this.option.diffSetting ? 
            this.option.borderRadius + 'px' : 
            `${this.option.topLeftBorderRadius + 'px' || 0} ${this.option.topRightBorderRadius + 'px' || 0} ${this.option.bottomRightBorderRadius + 'px' || 0} ${this.option.bottomLeftBorderRadius + 'px' || 0}`
        }
    },
}
</script>
<style lang="scss" scoped>
.graph-wrapper{
    width: 100%;
    height: 100%;
}
.graph{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}
</style>