<template>
    <div class="card-wrapper" :style="{
        width: component.width + 'px',
        height: component.height + 'px',
    }">
        <div class="border-top-line" :style="{
            borderTop: `${borderTopWidth} ${borderTopStyle} ${borderTopColor}`
        }"></div>
        <div class="border-right-line" :style="{
            borderRight: `${borderRightWidth} ${borderRightStyle} ${borderRightColor}`
        }"></div>
        <div class="border-bottom-line" :style="{
            borderBottom: `${borderBottomWidth} ${borderBottomStyle} ${borderBottomColor}`
        }"></div>
        <div class="border-left-line" :style="{
            borderLeft: `${borderLeftWidth} ${borderLeftStyle} ${borderLeftColor}`
        }"></div>
        <div class="top-left-corner" :style="{
            width: topLeftSize,
            height: topLeftSize,
            borderTopWidth: borderTopWidth,
            borderLeftWidth: borderLeftWidth,
            borderColor: topLeftBorderColor,
            borderStyle: `${borderTopStyle} none none ${borderLeftStyle}`
        }"></div>
        <div class="top-right-corner" :style="{
            width: topRightSize,
            height: topRightSize,
            borderTopWidth: borderTopWidth,
            borderRightWidth: borderRightWidth,
            borderColor: topRightBorderColor,
            borderStyle: `${borderTopStyle} ${borderRightStyle} none none`
        }"></div>
        <div class="bottom-left-corner" :style="{
            width: bottomLeftSize,
            height: bottomLeftSize,
            borderBottomWidth: borderBottomWidth,
            borderLeftWidth: borderLeftWidth,
            borderColor: bottomLeftBorderColor,
            borderStyle: `none none ${borderBottomStyle} ${borderLeftStyle}`
        }"></div>
        <div class="bottom-right-corner" :style="{
            width: bottomRightSize,
            height: bottomRightSize,
            borderBottomWidth: borderBottomWidth,
            borderRightWidth: borderRightWidth,
            borderColor: bottomRightBorderColor,
            borderStyle: `none ${borderRightStyle} ${borderBottomStyle} none`
        }"></div>
        <div class="background" :style="{
            backgroundColor,
            opacity
        }"></div>
    </div>
</template>
<script>
export default {
    name: "card",
    props: {
        option: Object,
        component: Object,
    },
    computed: {
        borderTopWidth() {
            return this.option.borderTopWidth + 'px';
        },
        borderRightWidth(){
            return this.option.borderRightWidth + 'px';
        },
        borderBottomWidth(){
            return this.option.borderBottomWidth + 'px';
        },
        borderLeftWidth(){
            return this.option.borderLeftWidth + 'px';
        },
        borderTopStyle(){
            return this.option.borderTopStyle;
        },
        borderRightStyle(){
            return this.option.borderRightStyle;
        },
        borderBottomStyle(){
            return this.option.borderBottomStyle;
        },
        borderLeftStyle(){
            return this.option.borderLeftStyle;
        },
        borderTopColor(){
            return this.option.borderTopColor
        },
        borderRightColor(){
            return this.option.borderRightColor
        },
        borderBottomColor(){
            return this.option.borderBottomColor
        },
        borderLeftColor(){
            return this.option.borderLeftColor
        },
        backgroundColor(){
            return this.option.backgroundColor
        },
        opacity(){
            return this.option.opacity / 100
        },
        topLeftSize(){
            return this.option.topLeftSize + 'px'
        },
        topRightSize(){
            return this.option.topRightSize + 'px'
        },
        bottomLeftSize(){
            return this.option.bottomLeftSize + 'px'
        },
        bottomRightSize(){
            return this.option.bottomRightSize + 'px'
        },
        topLeftBorderColor(){
            const size = this.option.topLeftSize
            const color = this.option.topLeftBorderColor
            return size ? `${color} transparent transparent ${color}` : 'transparent'
        },
        topRightBorderColor(){
            const size = this.option.topRightSize
            const color = this.option.topRightBorderColor
            return size ? `${color} ${color}  transparent transparent` : 'transparent'
        },
        bottomLeftBorderColor(){
            const size = this.option.bottomLeftSize
            const color = this.option.bottomLeftBorderColor
            return size ? `transparent transparent ${color} ${color}` : 'transparent'
        },
        bottomRightBorderColor(){
            const size = this.option.bottomRightSize
            const color = this.option.bottomLeftBorderColor
            return size ? `transparent ${color} ${color} transparent` : 'transparent'
        },
    },
    created() {
    },
};
</script>
<style lang="scss" scoped>
.card-wrapper{
    position: relative;
    box-sizing: border-box;
    .border-top-line{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 0;
    }
    .border-right-line{
        position: absolute;
        right: 0;
        top: 0;
        width: 0;
        height: 100%;
    }
    .border-bottom-line{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 0;
    }
    .border-left-line{
        position: absolute;
        top: 0;
        left: 0;
        width: 0;
        height: 100%;
    }
    .top-left-corner{
        position: absolute;
        top: 0;
        left: 0;
    }
    .top-right-corner{
        position: absolute;
        top: 0;
        right: 0;
    }
    .bottom-left-corner{
        position: absolute;
        bottom: 0;
        left: 0;
    }
    .bottom-right-corner{
        position: absolute;
        bottom: 0;
        right: 0;
    }
    .background{
        width: 100%;
        height: 100%;
    }
}
</style>
