<template>
    <div class="tabs-wrap">
        <div class="tabs-title"><i class="el-icon-arrow-right"></i>物业信息</div>
        <div class="tabs-charts">
            <v-chart autoresize :option="option" />
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            option: {
                legend: {
                    bottom: 0,
                    left: 0,
                    orient: 'horizontal',
                    icon: "circle",
                    formatter: (name)=> {
                        return `${name}        ${name === "已收" ? 75 : 25}元        ${name === "已收" ? 75 : 25}%`
                    },
                    textStyle: {
                        color: '#fff'
                    }
                },
                series: [
                    {
                        name: "物业信息",
                        type: "pie",
                        center: ["25%", "25%"],
                        radius: ["35%", "45%"],
                        label: {
                            show: true,
                            position: "center",
                            formatter: ["{percent| 75%}", "{title| 已收}"].join("\n"),
                            rich: {
                                percent: {
                                    padding: [60, 0, 0],
                                    fontSize: 14,
                                    color: '#fff'
                                },
                                title: {
                                    padding: [40, 0, 0],
                                    fontSize: 14,
                                    color: '#fff'
                                },
                            },
                        },
                        data: [
                            {
                                name: '已收',
                                value: 75,
                                itemStyle: {
                                    color: "#005bab",
                                },
                            },
                            {
                                value: 100 - 75,
                                itemStyle: {
                                    color: "rgba(255, 255, 255, .2)",
                                },
                            },
                        ],
                    },
                    {
                        name: "物业信息",
                        type: "pie",
                        center: ["75%", "25%"],
                        radius: ["35%", "45%"],
                        label: {
                            show: true,
                            position: "center",
                            formatter: ["{percent| 25%}", "{title| 未收}"].join("\n"),
                            rich: {
                                percent: {
                                    padding: [60, 0, 0],
                                    fontSize: 14,
                                    color: '#fff'
                                },
                                title: {
                                    padding: [40, 0, 0],
                                    fontSize: 14,
                                    color: '#fff'
                                },
                            },
                        },
                        data: [
                            {
                                name: "未收",
                                value: 25,
                                itemStyle: {
                                    color: "#eea91c",
                                },
                            },
                            {
                                value: 100 - 25,
                                itemStyle: {
                                    color: "rgba(255, 255, 255, .2)",
                                },
                            },
                        ],
                    },
                ],
            },
        };
    },
};
</script>
