import { url } from '@/config';
//导入主题文件
import '@/theme/index.js';
import dataV from '@jiaminghi/data-view';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import $ from 'jquery';
import moment from 'moment';
import Vue from 'vue';
import Vuex from 'vuex';
import App from './App.vue';
import axios from './axios';
//注册自定义组件
import './components/';
import router from './router.js';
import store from './store';
import './styles/common.scss';
import { dataToChart } from './tool';
import ECharts from 'vue-echarts'
import { use } from 'echarts/core'
import { CanvasRenderer } from 'echarts/renderers'
import {registerMap} from 'echarts'
import zhongshan from './map/zhongshan.json'
import * as charts from 'echarts/charts'
import * as EchartsComponts from 'echarts/components'

let chartsList = [];
for(let k in charts){
  chartsList.push(charts[k])
}

let EComponent = []
for(let k in EchartsComponts){
    EComponent.push(EchartsComponts[k])
}

use([
  CanvasRenderer,
  ...chartsList,
  ...EComponent
]);

// console.log(echarts);
registerMap('zscity', zhongshan)

Vue.component('v-chart', ECharts)

moment.locale('zh-cn')
window.axios = axios;
window.dataToChart = dataToChart
window.$ = $
Vue.config.productionTip = false
Vue.prototype.url = url;
Vue.use(window.AVUE, {
  size: 'mini'
});
Vue.use(ElementUI);
Vue.use(dataV)
Vue.use(Vuex)
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
